body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: 'Poppins', sans-serif;
}

.App {
  font-family: 'Poppins', sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  font-family: 'Poppins', sans-serif;
}


#top {
    /* width: 73%; */
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#top-phone {
    display: flex;
    align-items: center;
}

#top-phone ion-icon {
    font-size: 1.3rem;
}

#top-phone p {
    font-size: 1.2rem;
}

#top-phone p {
    margin-top: 15px;
    margin-left: 5px;
}

#top-other ion-icon {
    margin: 0 5px;
    font-size: 1.3rem;
}

#top .hov:hover {
    cursor: pointer;
}

@media screen and (max-width: 1024px) {
    #top {
        width: 95%;
    }
}


#logo {
    font-size: 1.5rem;
    margin-right: 2rem;
}


/* #footer {
    background-color: #FFCD58;
} */


#reviews-page h2 {
    font-size: 2rem;
}

#reviews-page p {
    font-size: 1.2rem;
}

#reviews-page img {
    width: 15%;
}


#head {
    background-image: -webkit-gradinet(linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6))), url(/korlake-restaurant/static/media/dinner-background.0eb19e38.jpg);
    background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url(/korlake-restaurant/static/media/dinner-background.0eb19e38.jpg);
    background-size: cover;
    background-position: center;
}

#head h1 {
    font-size: 5rem;
}

#head h2,
#head h1,
#top-button,
#top-reservation-button {
    animation: fadeInAnimation ease 3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

/* SECOND_SECTION */

#second-img img {
    width: 100%;
}

/* MENU-SECTION */

#menu-section {
    background-color: rgb(33,37,41);
}

/* FOOD SECTION */

.food-div p {
    font-size: 2.3rem;
}

.food-div img {
    -webkit-filter: brightness(40%);
            filter: brightness(40%);
}

/* MEDIA */

@media screen and (min-width: 2500px) {
    #head h2 {
        font-size: 4rem;
    }

    #head h1 {
        font-size: 6rem;
    }

    .food-div p {
        font-size: 3rem;
    }
}

@media screen and (max-width: 1200px) {
    .food-div p {
        font-size: 1.9rem;
    }
}

@media screen and (max-width: 992px) {
    #head h1 {
        font-size: 3.5rem;
    }

    #head h2 {
        font-size: 2rem;
    }

    .food-div p {
        font-size: 2.2rem;
    }
}

@media screen and (max-width: 576px) {

    #head h1 {
        font-size: 2.5rem;
    }

    #head h2 {
        font-size: 1.5rem;
    }

    #menu-section .dish-card {
        padding: 0;
    }

    .food-div p {
        font-size: 2.5rem;
    }
}


.menu-product img {
    width: 100%;
}

#lunch img {
    transform: scaleX(-1);
}

.background-dark,
#lunch .card,
#dessert .card {
    background-color: rgb(33,37,41);
}

.menu-product h2 {
    font-size: 3rem;
}

@media screen and (max-width: 992px) {
    .menu-product h2 {
        font-size: 2.3rem;
    }
}


.card-title strong {
    font-size: 1.5rem;
}

.product-price {
    font-size: 1.3rem;
}

@media screen and (max-width: 992px) {
    .card-title strong {
        font-size: 1.3rem;
    }
}


#reservation-page h2 {
    margin-bottom: 2rem;
}

/* MODAL */

#results #modal {
    display: flex;
    align-items: center;
}

#results #modal .btn-close {
    width: 30px;
    height: 30px;
    border-radius: 50%;
}


.our-story {
    background-color: rgb(33,37,41);
}

.our-story img {
    width: 75%;
}

#allstory h1 {
    font-size: 3rem;
}

#allstory h3 {
    font-size: 2rem;
}

#allstory h4 {
    font-size: 2rem;
}

#header-img {
    width: 100%;
}

.images-gallery img {
    width: 100%;
}

#stage {
    width: 50%;
    margin: 5rem 0;
}

@media screen and (max-width: 992px) {
    #allstory h1 {
        font-size: 2.3rem;
    }

    .our-story img {
        width: 100%;
    }
}


#contact-page h1 {
    font-size: 3rem;
}

#allcontacts h1 {
    padding: 5%;
}

#phone img {
    width: 100%;
}

#allcontacts ion-icon {
    font-size: 1.4rem;
}

#allcontacts .hov:hover {
    cursor: pointer;
}

#contact-page #reviews-page {
    background-color: rgb(33,37,41);
}

#contact-page #reviews-page h2 {
    color: #FFFFFF;
}

@media screen and (max-width: 992px) {
    #contact-page h1 {
        font-size: 2.3rem;
    }

    /* #reservations-div {
        padding: 0;
    } */
}



